@import url('https://fonts.googleapis.com/css?family=Heebo:regular,bold,light&subset=latin,latin-ext');

:root {
  --bg-color: white;
  --font-color: black;
  --school-hover-color: rgba(59, 125, 161, 0.40);
  --intern-hover-color: rgba(255, 153, 0, 0.60);
  --codebase-hover-color: rgba(47, 59, 121, 0.30);
  --uma-hover-color: rgba(255, 102, 51, 0.40);
  font-family: Helvetica;
}

[data-theme="dark"] {
  --bg-color: #121212;
  --font-color: #ffffffc5;
  --school-hover-color: rgba(59, 125, 161, 0.40);
  --intern-hover-color: rgba(255, 153, 0, 0.60);
  --codebase-hover-color: rgba(171, 182, 233, 0.3);
  --uma-hover-color: rgba(233, 140, 109, 0.4);
}

body {
  background-color: var(--bg-color);
  color: var(--font-color);
  transition: .4s;
}

.theme-switch-wrapper {
  display: flex;
  align-items: center;
}

.theme-switch {
  height: 34px;
  position: absolute;
  top: 30px;
  right: 40px;
  width: 60px;
}

.theme-switch input {
  display:none;
}

.slider {
  background-color: #ccc;
  bottom: 0;
  cursor: pointer;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: .4s;
}

.slider:before {
  background-color: #fff;
  bottom: 4px;
  content: "";
  height: 26px;
  left: 4px;
  position:absolute;
  transition: .4s;
  width: 26px;
}

input:checked + .slider {
  background-color: #b8b8b8;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

@media screen and (max-width: 600px) {

  .body {
    display: flex;
  }

  .content-container{ 
    width: 100vw;
    height: 85vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .content {
    width: 350px;
    min-width: 350px;
  }
  
  .greeting-header {
    font-weight: bold;
    font-size: 28px;
    margin-left: 20px;
  }
  
  .greeting-text {
    font-size: 13px;
    margin-left: 20px;
  }
  
  .about-points {
    font-size: 13px;
    list-style-type: none;
  }
  
  .links-container {
    margin-top: 30px;
    display: flex; 
    justify-content: center;
  }
  
  .links {
    color: #828282;
    width: 80%;
    display: flex;
    flex-direction: row;
    font-size: 12px;
    justify-content: space-evenly;
  }
  
  a.link-text {
    color: #828282;
    text-decoration: none;
  }
  
  a.link-text:hover {
    color: black;
  }
  
  a.about-links {
    color: var(--font-color);
    text-decoration: none;
  }
  
  .school{
    font-weight: bold;
    background-image: linear-gradient(var(--school-hover-color), var(--school-hover-color));
    background-repeat: no-repeat;
    background-position: 0% 100%;
    background-size: 100% 2.5px;
    transition: background-size .2s ease-in .1s;
  }
  
  .school:hover {
    transition: background-size .2s ease-in .1s;
    background-size: 100% 100%;
    background-position: 0% 100%;
  }
  
  .intern {
    font-weight: bold; 
    background-image: linear-gradient(var(--intern-hover-color), var(--intern-hover-color));
    background-repeat: no-repeat;
    background-position: 0% 100%;
    background-size: 100% 2.5px;
    transition: background-size .2s ease-in .1s;
  }
  
  .intern:hover {
    transition: background-size .2s ease-in .1s;
    background-size: 100% 100%;
    background-position: 0% 100%;
  }
  
  .codebase {
    font-weight: bold; 
    background-image: linear-gradient(var(--codebase-hover-color), var(--codebase-hover-color));
    background-repeat: no-repeat;
    background-position: 0% 100%;
    background-size: 100% 2.5px;
    transition: background-size .2s ease-in .1s;
  }
  
  .codebase:hover {
    transition: background-size .2s ease-in .1s;
    background-size: 100% 100%;
    background-position: 0% 100%;
  }
  
  .uma {
    font-weight: bold; 
    background-image: linear-gradient(var(--uma-hover-color, var(--uma-hover-color)));
    background-repeat: no-repeat;
    background-position: 0% 100%;
    background-size: 100% 2.5px;
    transition: background-size .2s ease-in .1s;
  }
  
  .uma:hover {
    transition: background-size .2s ease-in .1s;
    background-size: 100% 100%;
    background-position: 0% 100%;
  }
  
  ul {
    line-height: 150%;
  }
  
  .wave:hover {
    animation-name: wave-animation; 
    animation-duration: 1.2s;       
    animation-iteration-count: infinite; 
    transform-origin: 70% 70%;      
    display: inline-block;
  }
  
  @keyframes wave-animation {
    0% { transform: rotate( 0.0deg) }
   10% { transform: rotate(14.0deg) }  
   20% { transform: rotate(-8.0deg) }
   30% { transform: rotate(14.0deg) }
   40% { transform: rotate(-4.0deg) }
   50% { transform: rotate(10.0deg) }
   60% { transform: rotate( 0.0deg) } 
  100% { transform: rotate( 0.0deg) }
  }
}

