@import url(https://fonts.googleapis.com/css?family=Heebo);
@import url(https://fonts.googleapis.com/css?family=Heebo:regular,bold,light&subset=latin,latin-ext);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Heebo"
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --bg-color: white;
  --font-color: black;
  --school-hover-color: rgba(59, 125, 161, 0.40);
  --intern-hover-color: rgba(255, 153, 0, 0.60);
  --codebase-hover-color: rgba(47, 59, 121, 0.30);
  --uma-hover-color: rgba(255, 102, 51, 0.40);
  font-family: Helvetica;
}

[data-theme="dark"] {
  --bg-color: #121212;
  --font-color: #ffffffc5;
  --school-hover-color: rgba(59, 125, 161, 0.40);
  --intern-hover-color: rgba(255, 153, 0, 0.60);
  --codebase-hover-color: rgba(171, 182, 233, 0.3);
  --uma-hover-color: rgba(233, 140, 109, 0.4);
}

body {
  background-color: white;
  background-color: var(--bg-color);
  color: black;
  color: var(--font-color);
  -webkit-transition: .4s;
  transition: .4s;
}

.theme-switch-wrapper {
  display: flex;
  align-items: center;
}

.theme-switch {
  height: 34px;
  position: absolute;
  top: 30px;
  right: 40px;
  width: 60px;
}

.theme-switch input {
  display:none;
}

.slider {
  background-color: #ccc;
  bottom: 0;
  cursor: pointer;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  background-color: #fff;
  bottom: 4px;
  content: "";
  height: 26px;
  left: 4px;
  position:absolute;
  -webkit-transition: .4s;
  transition: .4s;
  width: 26px;
}

input:checked + .slider {
  background-color: #b8b8b8;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
          transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

